import Konva from 'konva';
import { Stage } from 'konva/lib/Stage';
import { Fragment, useEffect, useRef, useState } from 'react';

function zoomStage(stage: Stage, scaleBy: number) {
  const oldScale = stage.scaleX();

  const pos = {
    x: stage.width() / 2,
    y: stage.height() / 2,
  };
  const mousePointTo = {
    x: pos.x / oldScale - stage.x() / oldScale,
    y: pos.y / oldScale - stage.y() / oldScale,
  };

  //const newScale = Math.max(0.05, oldScale * scaleBy);
  const newScale = oldScale + scaleBy;

  const newPos = {
    x: -(mousePointTo.x - pos.x / newScale) * newScale,
    y: -(mousePointTo.y - pos.y / newScale) * newScale,
  };

  console.log(newScale + "");

  // const newAttrs = limitAttributes(stage, { ...newPos, scale: newScale });

  // stage.to({
  //   x: newAttrs.x,
  //   y: newAttrs.y,
  //   scaleX: newAttrs.scale,
  //   scaleY: newAttrs.scale,
  //   duration: 0.1,
  // });
  stage.to({
    x: newPos.x,
    y: newPos.y,
    scaleX: newScale,
    scaleY: newScale,
    duration: 0.1,
  });

  stage.width(1000 * newScale);
  stage.height(1000 * newScale);


}

function limitAttributes(stage: Stage, newAttrs: { x: number, y: number, scale: number }) {
  const box: {
    x: number;
    y: number;
    width: number;
    height: number;
  } = {
    x: 500,
    y: 500,
    width: 1000,
    height: 1000
  };

  const minX = -box.width + stage.width() / 2;
  const maxX = stage.width() / 2;

  const x = Math.max(minX, Math.min(newAttrs.x, maxX));

  const minY = -box.height + stage.height() / 2;
  const maxY = stage.height() / 2;

  const y = Math.max(minY, Math.min(newAttrs.y, maxY));

  const scale = Math.max(0.05, newAttrs.scale);

  return { x, y, scale };
}


function StickersScreen() {
  const container = useRef<HTMLDivElement>(null);

  const [myVariable, setMyVariable] = useState<Stage>();

  //const stageRef = useRef<Stage>();

  useEffect(() => {
    let WIDTH = 1000;
    let HEIGHT = 1000;
    let NUMBER = 20;

    var stage = new Konva.Stage({
      container: container.current!,
      width: WIDTH,
      height: HEIGHT,
    });

    setMyVariable(stage);

    var layer = new Konva.Layer();
    stage.add(layer);

    function generateNode() {
      return new Konva.Circle({
        x: WIDTH * Math.random(),
        y: HEIGHT * Math.random(),
        radius: 50,
        fill: 'red',
        stroke: 'black',
      });
    }

    for (var i = 0; i < NUMBER; i++) {
      layer.add(generateNode());
    }
  }, []);

  return (
    <Fragment >
      {/* <input type="Zoom" id="zoom" value="Zoom!" /> */}

      <div className='base-container'>

        <div className="zoom-container">
          <button
            onClick={() => {
              //zoomStage(stage, 1.2);
              zoomStage(myVariable!, 0.15);
            }}
          >
            +
          </button>
          <button
            onClick={() => {
              zoomStage(myVariable!, -0.15);
            }}
          >
            -
          </button>
        </div>
        <div className="canvas-container" ref={container}></div>

      </div>

    </Fragment>
  );
}

export default StickersScreen;