import { Routes, Route, BrowserRouter } from 'react-router-dom';
import StarsScreen from '../stars-screen/stars-screen';
import MainScreen from '../main-screen/main-screen';
import { AppRoute } from '../../const';
import NotFoundScreen from '../not-found-screen/not-found-screen';
import RectScreen from '../rect-screen/rect-screen';
import ZoomDemoScreen from '../zoom-demo-screen/zoom-demo-screen';
import StickersScreen from '../stickers-screen/stickers-screen';

function App(): JSX.Element {

  return (
    <BrowserRouter>
      <Routes>
        <Route
          path={AppRoute.Main}
          element={<MainScreen />}
        />
        <Route
          path={AppRoute.Stickers}
          element={<StickersScreen />}
        />
        <Route
          path={AppRoute.Stars}
          element={<StarsScreen />}
        />
        <Route
          path={AppRoute.Rect}
          element={<RectScreen />}
        />
        <Route
          path={AppRoute.ZoomDemo}
          element={<ZoomDemoScreen />}
        />
        <Route
          path="*"
          element={<NotFoundScreen />}
        />
      </Routes>
    </BrowserRouter>
  );
}

export default App;
